.feedback__form-fieldset {
  margin: 0;
  padding: 0 20px;
  border: none;
}

.feedback__form-required--field {
  display: none;
}

.feedback__form-button--wrap {
  display: flex;
  padding: 0 20px 37px;
  justify-content: center;
}

.feedback__form-button {
  flex-grow: 1;
}

@media (min-width: $tablet-width) {
    .feedback__form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding: 0 60px;
      position: relative;
    }

    .feedback__form::before {
      content: "";
      position: absolute;
      width: 292px;
      height: 1px;
      background-color: $border-color;
      z-index: 100;
      left: 50%;
      top: 148px;
      transform: translate(-50%) rotate(90deg);
    }

    .feedback__form-fieldset {
      padding: 0;
    }

    .feedback__form-button--wrap {
      grid-row: 5/6;
      grid-column: 1/3;
      justify-content: space-between;
      padding: 0;
      margin-bottom: 61px;
    }

    .feedback__form-button {
      flex-grow: 0;
      width: 324px;
      padding: 15px 18px 11px 7px;
    }

    .feedback__form-required--field {
      display: block;
      margin-top: 23px;
    }
  }

  @media (min-width: $desktop-width) {
    .feedback__form {
      padding: 30px 120px;
    }

    .feedback__form-button {
      flex-grow: 0;
      width: 480px;
      padding: 15px 5px 11px 7px;
    }

    .feedback__form-button--wrap {
      margin-bottom: 65px;
    }
  }



