
.copyright__text {
  font-size: 14px;
  line-height: 26px;
  margin: 0;
  padding-bottom: 21px;
  ;
}

.copyright__link {
  position: relative;
  display: block;
  color: $black;
  padding-right: 38px;
}

.copyright__logo {
  position: absolute;
  display: block;
  right: -1px;
  top: -4px;
  width: 27px;
  height: 34px;
  fill: $black;

  &:hover {
    fill: $background-btn;
  }

  &:active {
    fill: $black;
    opacity: 0.3;
  }
}

