.advantages-list {
    @include mixins;
  }

  @media (min-width: $tablet-width) {
    .advantages-list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .advantages-list--first {
    padding: 35px 20px 9px;
  }

  .comma,
  .but {
    display: none;
  }

  @media (min-width: $tablet-width) {
    .advantages-list--first {
      margin: 0;
      padding: 160px 0 59px;
      text-align: center;
    }

    .advantages-list__item--first:nth-child(2) {
      grid-row: 1/2;
      grid-column: 3/4;
    }

    .advantages-list__item--first:nth-child(1) .advantages-list__item-description {
      padding: 0 35px;
    }

    .advantages-list__item--first:nth-child(2) .advantages-list__item-description {
      padding: 0 32px;
    }

    .advantages-list__item--first:nth-child(3) .advantages-list__item-description {
      padding: 0 52px;
    }

    .but {
      display: block;
    }
  }

  @media (min-width: $desktop-width) {
    .advantages-list--first {
      padding-bottom: 80px;
    }

    .advantages-list__item--first:nth-child(1) .advantages-list__item-description {
      padding: 0 74px;
    }

    .advantages-list__item--first:nth-child(2) .advantages-list__item-description{
      padding: 0 80px;
    }

    .but {
      display: none;
    }

    .comma {
      display: block;
    }
  }

  .advantages-list__item--first {
    position: relative;
  }

  .advantages-list__item--house::before {
    content: "";
    position: absolute;
    width: 39px;
    height: 37px;
    top: -10px;
    left: 0;
    background-image: url("../img/icon-house.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 39px 37px;
  }

  .advantages-list__item--gift::before {
    content: "";
    position: absolute;
    width: 32px;
    height: 38px;
    top: -10px;
    left: 5px;
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 32px 38px;
  }

  .advantages-list__item--burger::before {
    content: "";
    position: absolute;
    width: 40px;
    height: 35px;
    top: -5px;
    left: 0;
    background-image: url("../img/icon-burger.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 40px 35px;
  }

  @media (min-width: $tablet-width) {
    .advantages-list__item--house::before {
      top: -98px;
      left: 50%;
      transform: translate(-50%);
      width: 75px;
      height: 72px;
      background-size: 75px 72px;
    }

    .advantages-list__item--gift::before {
      top: -101px;
      left: 50%;
      transform: translate(-50%);
      width: 64px;
      height: 75px;
      background-size: 64px 75px;
    }

    .advantages-list__item--burger::before {
      top: -95px;
      left: 50%;
      transform: translate(-50%);
      width: 76px;
      height: 68px;
      background-size: 76px 68px;
    }
  }

  .advantages-list__item--first .advantages-list__item-title {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    color: $font-color-title;
    margin: 0;
    padding-left: 49px;
    padding-bottom: 17px;
  }

  .advantages-list__item--first .advantages-list__item-description {
    padding-left: 50px;
    padding-bottom: 25px;
  }

  @media (min-width: $tablet-width) {
    .advantages-list__item--first .advantages-list__item-title {
      font-size: 21px;
      padding-left: 0;
      padding-bottom: 22px;
        // padding-bottom: 25px;

    }
  }

  .advantages-list__item-description {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: $font-color-base;
    margin: 0;
    padding-bottom: 25px;
  }

  .advantages-list__item-number {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: $font-color-base;
    padding-bottom: 27px;
    margin: 0;
  }

  .advantages-list--gray {
    background-color: $background-block;
    text-align: center;
  }

  .advantages-list--gray .advantages-list__item {
    padding-top: 13px;
    margin: 0 20px;
  }

  .advantages-list__item--gray:nth-child(2) {
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
  }

  .advantages-list__item--gray .advantages-list__item-title {
    font-size: 18px;
    margin: 0;
    padding-top: 16px;
    padding-bottom: 12px;
  }

  .advantages-list__item--gray .advantages-list__item-number {
    padding-bottom: 19px;
  }

  .advantages-list__item--gray .advantages-list__item-description {
    padding-bottom: 22px;
  }

  .advantages-list__item--gray:nth-child(2) .advantages-list__item-description {
    padding: 0 20px 22px;
  }

  .advantages-list__item--gray:nth-child(3) .advantages-list__item-description {
    padding: 0 15px 25px;
  }

  @media (min-width: $tablet-width) {
    .advantages-list__item--gray:nth-child(2) {
      border: none;
    }

    .advantages-list__item--gray .advantages-list__item-title {
      font-size: 21px;
      line-height: 26px;
      padding-top: 30px;
    }

    .advantages-list__item--gray .advantages-list__item-number {
      padding-bottom: 23px;
    }

    .advantages-list__item--gray:nth-child(2) .advantages-list__item-title {
      padding: 30px 20px 12px;
    }

    .advantages-list__item--gray:nth-child(2) .advantages-list__item-description{
      padding: 0 37px 42px;
    }

    .advantages-list__item--gray:nth-child(3) .advantages-list__item-title {
      padding: 30px 30px 12px;
    }

    .advantages-list__item--gray:nth-child(3) .advantages-list__item-description {
      padding: 0 25px 42px;
    }
  }

  @media (min-width: $desktop-width) {
    .advantages-list__item--gray:nth-child(1) .advantages-list__item-title {
      padding: 30px 70px 12px;
    }

    .advantages-list__item--gray:nth-child(1) .advantages-list__item-description {
      padding: 0 30px 63px;
    }

    .advantages-list__item--gray:nth-child(2) .advantages-list__item-title {
      padding: 30px 92px 12px;
    }

    .advantages-list__item--gray:nth-child(2) .advantages-list__item-description {
      padding: 0 45px 63px;
    }

    .advantages-list__item--gray:nth-child(3) .advantages-list__item-title {
      padding: 30px 105px 12px;
    }
  }
