.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 320px;
    background-color: $white;
    text-align: center;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    z-index: 100;
  }

  .pop-up--close {
    display: none;
  }

  .modal__text-box {
    padding: 27px 35px 26px;

    &--check {
      padding-top: 19px;
      padding-bottom: 0;
    }
  }

  .modal__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 26px;
    color: $black;
  }

  .modal__subtitle {
    line-height: 24px;

    &--check {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  .modal__button-box {
    padding-top: 20px;
    padding-bottom: 20px;

    &--successfully {
      background-color: $background-input;
    }

    &--unsuccesfully {
      background-color: $white;
      padding-bottom: 35px;
    }
  }


  .modal__button {
    margin: 0 auto;
    font-size: 18px;

    &--done {
      min-width: 280px;
    }

    &--check {
      width: 180px;
    }
  }

  @media (min-width: $tablet-width) {
    .modal {
      &--done {
        width: 640px;
      }

      &--check {
        width: 400px;
      }
    }

    .modal__text-box {
      padding-left: 80px;
      padding-right: 80px;

      &--check {
        padding: 30px 60px 15px;
      }
    }

    .modal__title {
      margin-top: 30px;
      margin-bottom: 29px;
      font-size: 30px;
      line-height: 30px;
    }

    .modal__subtitle--check {
      padding-left: 0;
      padding-right: 0;
    }

    .modal__button-box {
      padding-top: 44px;
      padding-bottom: 44px;

      &--check {
        padding-top: 15px;
        padding-bottom: 52px;
      }
    }

    .modal__button {
      font-size: 21px;

      &--done {
        width: 460px;
      }

      &--check {
        width: 270px;
      }
    }
  }
