.gallery {
  @include mixins;
}

.gallery__photo {
  display: block;
  width: 100%;
  height: auto;
}

.gallery__item {
  padding-top: 21px;
  padding-bottom: 18px;
  border-bottom: 1px solid $border-color;
}

.gallery__image {
  // margin: 0 auto;
  padding: 0 20px;
}

.gallery__sign {
  margin: 0 auto;
  padding: 0 20px;
}

.gallery__like-button {
  padding: 0;
  margin-right: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.gallery__like-icon {
  width: 23px;
  height: 21px;
  fill: rgba($black, 0.3);
}


@media (min-width: $tablet-width) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 60px;
  }
  .gallery__image {
    padding: 0;
  }

   .gallery__item {
    border: none;
    padding: 0;
  }

  .gallery__item:nth-child(1) {
    grid-row: 1/2;
    grid-column: 1/3;
  }

  .gallery__item:nth-child(-n+2) .gallery__sign {
    background-color: $background-input;
  }

  .gallery__item:nth-child(n+3):nth-child(-n+4) .gallery__sign {
    background-color: $background-color-photo;
  }

  .gallery__sign {
    display: grid;
    padding: 0;
  }

  .gallery__name {
    padding-left: 20px;
  }

  .gallery__like {
    grid-row: 1/3;
    grid-column: 2/3;
    text-align: right;
    // padding: 40px 36px 0;
    padding-top: 26px;
  }

  .gallery__item:nth-child(1) .count {
    margin-right: 20px;
  }

  .gallery__item:nth-child(2) .count,
  .gallery__item:nth-child(4) .count {
    margin-right: 30px;
  }

  .gallery__item:nth-child(3) .count {
    margin-right: 35px;
  }

  .gallery__like-button {
    margin-right: 6px;
  }
}



@media (min-width: $desktop-width) {
  .gallery {
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }

  .gallery__item:nth-child(1) {
    grid-row: 1/2;
    grid-column: 1/4;
    height: 400px;
  }

  .gallery__item:nth-child(2) {
    grid-row: 2/3;
    grid-column: 1/2;
  }

  .gallery__item:nth-child(3) {
    grid-row: 2/3;
    grid-column: 2/3;
  }

  .gallery__item:nth-child(4) {
    grid-row: 2/3;
    grid-column: 3/4;
  }

  .gallery__item:not(:nth-child(1)) {
    height: 267px;
  }

  .gallery__sign {
    position: relative;
    top: -80px;
  }

  .gallery__item:nth-child(-n+2) .gallery__sign{
    background-color: $black;
    opacity: 0.3;
  }

  .gallery__item:nth-child(n+3):nth-child(-n+4) .gallery__sign {
    background-color: $black;
    opacity: 0.3;
  }
}


.gallery__name {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-align: left;
  margin: 0;
  padding: 9px 0 0 0;
  color: $font-color-title;
}

.gallery__author {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  text-align: left;
  color: $font-color-base;
  margin: 0 0 11px 0;
  padding: 0;
  text-transform: none;
}

.gallery__like {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: $font-color-title;
}

@media (min-width: $tablet-width) {
  .gallery__name {
    padding-left: 21px;
    padding-top: 16px;
  }

  .gallery__author {
    padding-left: 21px;
    padding-bottom: 16px;
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .gallery__name {
    color: $white;
  }

  .gallery__author {
    color: $white;
  }

  .gallery__like {
    color: $white;
  }

  .gallery__like-icon {
    fill: rgba($white, 1);
  }
}
